.centered {
    width: 100%;
    height: 110px;
    margin: auto;
  }
  
  .group {
    width: 16vw;
    height: 110px;
    overflow: hidden;
    position: relative;
  }
  
  label {
    position: absolute;
    top: 36.6666666667px;
    color: rgba(56, 56, 56, 0.417);
    font: 400 200% Roboto;
    cursor: text;
    -webkit-transition: .25s ease;
    transition: .25s ease;
  }
  
  input {
    display: block;
    width: 17vw;
    padding-top: 36.6666666667px;
    border: none;
    border-radius: 0;
    color: dimgrey;
    background: transparent;
    font-size: 36.6666666667px;
    -webkit-transition: .3s ease;
    transition: .3s ease;
  }
  input:valid ~ label {
    top: 0;
    font: 700 22px Roboto;
    color: rgba(143, 143, 143, 0.623);
  }
  input:focus {
    outline: none;
  }
  input:focus ~ label {
    top: 0;
    font: 700 22px Roboto;
    color: #2196f3;
  }
  input:focus ~ .bar:before {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  input:-webkit-autofill {
    box-shadow: 0 0 0px 1000px #333 inset;
    -webkit-text-fill-color: #ffffff !important;
  }
  
  .bar {
    background: #2195f3c9;
    content: '';
    width: 100%;
    height: 3.6666666667px;
    -webkit-transition: .3s ease;
    transition: .3s ease;
    position: relative;
  }
  .bar:before {
    content: '';
    position: absolute;
    width: 100%;
    height: 150%;
    background: #2196f3;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  
  ::-moz-selection {
    background: #2196f34d;
  }
  
  ::selection {
    background: #2196f34d;
  }

/* ==========button============ */

  body .btn-bg.bg-2 .btn-2 button {
    color: #ffffff;
    background: #2195f3a9;
    border: none;
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
    -webkit-transition: -webkit-transform 0.2s ease;
    transition: -webkit-transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  }
  body .btn-bg.bg-2 .btn-2 button:hover {
    background: transparent;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
    color: #2195f3b2;
  }
  body .btn-bg.bg-2 .btn-2 button:hover:before,
  body .btn-bg.bg-2 .btn-2 button:hover:after {
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
    width: 100%;
  }
  body .btn-bg.bg-2 .btn-2 button:before,
  body .btn-bg.bg-2 .btn-2 button:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0px;
    width: 0px;
    height: 3px;
    background: #2195f3b2;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
  }
  body .btn-bg.bg-2 .btn-2 button:after {
    top: inherit;
    left: inherit;
    bottom: 0;
    right: 0;
  }
  
  body .btn-bg.bg-2 .btn-2 button:active {
    -webkit-transform: translate(5px, 5px);
    transform: scale(0.9,0.9);
  }
  
